    margin-bottom: 8px;
  }
}
@media (max-width: 767px) {
  .about h2 {
    font-size: 28px;
    line-height: 39px;
  }
}

.about p {
  font-family: "PT Mono", "Monaco", monospace;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #041827;
  margin: 0;
}
.about p:not(:last-child) {
  margin-bottom: 19px;
}
@media (max-width: 767px) {
  .about p {
    font-size: 14px;
    line-height: 19px;
  }
