.social {
  display: flex;
  flex-direction: column;

  @include vp-1023 {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 35px;
  }

  @include vp-767 {
    position: relative;
    flex-direction: column;
    align-items: center;
    margin-bottom: 0;

    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 1px;
      background-color: $color-alice-blue-line;
      bottom: 0;
      left: 0;
    }
  }
}

.social__list {
  display: grid;
  grid-template-columns: repeat(3, 40px);
  column-gap: 15px;
  margin: 0;
  padding: 0;
  padding-left: 10px;
  list-style-type: none;
}

.social__link {
  display: block;
  padding: 5px;
}
